:root {
  --color-primary: #000000;
  --color-primary-disabled: rgb(225,225,225);
  --color-secondary: #FFFFFF;
  --color-header-bg: #F4D0CA;
  --color-challenge-message-bg: #B4C4FF;
  --color-challenge-active-bg: #FFEED3;
  --color-challenge-active-border: #FFC700;
  --gap-xsmall: 1.375em;
  --gap-small: 1.75em;
  --gap-normal: 2.375em;
  --gap-big: 2.75em;
  --section-gap: 1.75em 2.375em;
  --font-primary: 'Roboto', sans-serif;
  --font-secondary: 'PT Serif', serif; }

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
div,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  box-sizing: border-box;
  display: block;
  line-height: 1.5; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:is-active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

a {
  color: var(--color-primary);
  text-decoration: none;
  transition: all 125ms linear; }
  a.is-highlighted {
    font-weight: 700;
    text-decoration: underline; }
  a:hover {
    opacity: 0.5; }

.row {
  --row-max-width: 67.125em;
  max-width: var(--row-max-width);
  margin-left: auto;
  margin-right: auto; }
  .row-slim {
    --row-max-width: 42.5em; }

.navlist {
  list-style: none;
  margin: 0;
  padding: 0; }
  .navlist-inline {
    display: inline; }
    .navlist-inline .navlist-item {
      display: inline; }

.button {
  --btn-bg: var(--color-secondary);
  --btn-color: var(--color-primary);
  background: #FFFFFF;
  border: 3px solid;
  border-color: var(--btn-color);
  box-sizing: border-box;
  box-shadow: 10px 10px 0px var(--btn-color);
  display: inline-block;
  padding: 1.25em;
  min-width: 15.875em;
  color: var(--btn-color);
  text-transform: uppercase;
  text-align: center; }
  .button.is-disabled {
    --btn-color: var(--color-primary-disabled); }

.section-fullwidth {
  padding: var(--section-gap); }

.site-footer {
  margin-top: 10em;
  font-family: var(--font-secondary); }
